@import url(https://fonts.googleapis.com/css?family=Dosis:200,300,400,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Ropa+Sans);
a {
  text-decoration: none; }

.btn-primary {
  border-color: #2DC3B6 !important;
  background: transparent !important;
  color: #2DC3B6 !important;
  overflow: hidden;
  font-family: 'Dosis', sans-serif;
  font-weight: 200;
  text-transform: uppercase;
  font-weight: 700; }
  .btn-primary:after {
    width: 0;
    height: 0;
    opacity: 0;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    background-color: #2DC3B6 !important;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%); }
  .btn-primary:hover {
    color: #fff !important;
    background: transparent !important; }
    .btn-primary:hover:after {
      height: 250px;
      width: 250px;
      opacity: 1; }
  .btn-primary:active {
    color: #fff !important;
    background: transparent; }
    .btn-primary:active:after {
      height: 600px;
      width: 600px;
      opacity: 1; }

.btn {
  cursor: pointer;
  width: 160px;
  min-width: 160px;
  padding: 15px 0px;
  display: inline-block;
  border: 3px solid #33aab7;
  color: #33aab7;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.1em;
  font-weight: 400;
  outline: none;
  position: relative;
  z-index: 2;
  transition: all 0.3s; }
  .btn:after {
    content: '';
    position: absolute;
    z-index: -1;
    transition: all 0.3s; }
  .btn:focus {
    color: #33aab7; }

.btn-xl {
  color: #33aab7;
  background: transparent;
  border-color: #33aab7;
  font-family: 'Dosis', sans-serif;
  font-weight: 200;
  text-transform: uppercase;
  font-weight: 300;
  overflow: hidden;
  font-size: 18px; }
  .btn-xl:after {
    width: 101%;
    height: 0;
    opacity: 0;
    top: 50%;
    left: 50%;
    background-color: #33aab7;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%); }
  .btn-xl:hover {
    color: #fff; }
    .btn-xl:hover:after {
      height: 70%;
      opacity: 1; }
  .btn-xl:active {
    color: #fff; }
    .btn-xl:active:after {
      height: 130%;
      opacity: 1; }

.btn:focus,
.btn:active,
.btn.active,
.btn:active:focus {
  outline: none; }

.btn-large {
  width: 100%;
  min-width: 240px;
  max-width: 100%;
  margin: 0; }

.mat-ripple-element {
  display: none; }

.switch-wrapper {
  position: relative; }

.switch-wrapper > div {
  position: absolute;
  width: 100%; }

body {
  background-color: #12131a;
  margin: 0px;
  height: 100vh; }

* {
  outline: none; }

